div.simple-modal {
  position: absolute;
  max-width: 200px;
  background-color: #fff;

  /* ANIMATION */
  -webkit-transition-property: opacity transform;
  -moz-transition-property: opacity transform;
  -o-transition-property: opacity transform;
  transition-property: opacity transform;
  -webkit-transition-duration: 200ms;
  -moz-transition-duration: 200ms;
  -o-transition-duration: 200ms;
  transition-duration: 200ms;
}
div.simple-modal ul {
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
}
div.simple-modal li {
  display: block;
}

div.simple-modal.animate {
  zoom: 1;
  filter: alpha(opacity=0);
  -webkit-opacity: 0;
  -moz-opacity: 0;
  opacity: 0;

  -webkit-transform: scale(0);
  -moz-transform: scale(0);
  transform: scale(0);
  -webkit-transform-origin: 0 95%;
  -moz-transform-origin: 0 95%;
  transform-origin: 0 95%;
}
.inline-form {
  z-index: 20;
  position: relative;
  display: inline-block;
}
.inline-form input,
.inline-form button,
.inline-form span {
  padding: 0;
  margin: 0;
  width: auto;
  min-width: 20px;
  background-color: transparent;
  border: 0;
  border-bottom: 1px dashed #6691c2;
  text-align: center;
}
.inline-form input:disabled,
.inline-form button:disabled,
.inline-form span:disabled,
.inline-form input.disabled,
.inline-form button.disabled,
.inline-form span.disabled {
  color: #909090;
  border-bottom: 1px dashed #909090;
}
.inline-form .simple-modal {
  max-width: 200px;
  width: auto;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}
.inline-form .simple-modal button {
  width: 100%;
  padding: 8px 15px;
  border: 0;
  box-sizing: border-box;
}
.inline-form .simple-modal button:hover {
  background-color: #eee;
}
.inline-form .simple-modal.animate {
  transform-origin: 0 0%;
}
.spinner-container {
  display: block;
  width: 100%;
  height: 100%;
}
.global-notifications {
  z-index: 100;
  position: absolute;
  bottom: 10px;
  right: 10px;
}
.global-notifications .global-notification {
  display: inline-block;
  float: right;
  clear: right;
  max-width: 400px;
  padding: 20px 30px;
  margin-top: 10px;
  -webkit-transition: transform 200ms linear, transform 0.4s 0.2s ease;
  -moz-transition: transform 200ms linear, transform 0.4s 0.2s ease;
  -o-transition: transform 200ms linear, transform 0.4s 0.2s ease;
  transition: transform 200ms linear, transform 0.4s 0.2s ease;
}
.global-notifications .global-notification.error {
  background-color: #ef0009;
  color: #ffffff;
}
.global-notifications .global-notification.warning {
  background-color: #fecc09;
}
.global-notifications .global-notification.info {
  background-color: #127cff;
  color: #ffffff;
}
.global-notifications .global-notification.success {
  background-color: #23f113;
  color: #ffffff;
}
.global-notifications .global-notification.animate {
  -webkit-transform: translateX(1000px);
  -moz-transform: translateX(1000px);
  -ms-transform: translateX(1000px);
  -o-transform: translateX(1000px);
  transform: translateX(1000px);
}
.global-notifications .global-notification h1 {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
}
.global-notifications .global-notification p {
  margin: 0;
}
.global-notifications .global-notification button.cancel {
  margin-left: 10px;
